import React, { useEffect } from 'react';
import { useRouter } from 'next/router';

import { get_jroot_plot } from '../../../../config/config';
import {
  ParamsForApiProps,
  PlotDataProps,
  QueryProps,
} from '../../../../containers/display/interfaces';
import { useRequest } from '../../../../hooks/useRequest';
import {
  StyledCol,
  StyledPlotRow,
  PlotNameCol,
  MinusIcon,
  Column,
  ImageDiv,
} from '../../../../containers/display/styledComponents';
import { removePlotFromRightSide } from '../../plot/singlePlot/utils';
import { Button } from 'antd';

interface ZoomedJSROOTPlotsProps {
  selected_plot: PlotDataProps;
  params_for_api: ParamsForApiProps;
}

export const ZoomedJSROOTPlot = ({
  selected_plot,
  params_for_api,
}: ZoomedJSROOTPlotsProps) => {
  const router = useRouter();
  const query: QueryProps = router.query;

  const { data } = useRequest(get_jroot_plot(params_for_api), {}, [
    selected_plot.name, params_for_api.lumi
  ]);

  const Image = () => {
    return <ImageDiv
      id={`${selected_plot.name}${params_for_api.run_number}:${params_for_api.lumi}`}
      width={params_for_api.width}
      height={params_for_api.height}
    />
  }
  const element = !!document.getElementById(`${selected_plot.name}${params_for_api.run_number}:${params_for_api.lumi}`)

  useEffect(() => {
    //@ts-ignore
    ReactDOM.render(Image, document.getElementById(selected_plot.name));
    if (document.getElementById(`${selected_plot.name}${params_for_api.run_number}:${params_for_api.lumi}`)) {
      //@ts-ignore
      JSROOT.draw(`${selected_plot.name}${params_for_api.run_number}:${params_for_api.lumi}`, JSROOT.parse(JSON.stringify(data)), 'hist');
    }
  }, [data, params_for_api.lumi, element]);

  return (
    <StyledCol space={2}>
      <StyledPlotRow
        minheight={params_for_api.height}
        width={params_for_api.width}
        is_plot_selected={true.toString()}
        nopointer={true.toString()}
        id={selected_plot.name}
      >
        <PlotNameCol>{selected_plot.name}</PlotNameCol>
        <Column>
          <Button
            type="link"
            onClick={() => removePlotFromRightSide(query, selected_plot)}
            icon={<MinusIcon />}
          />
        </Column>
        {/* <ImageDiv
          id={`${selected_plot.name}${params_for_api.run_number}:${params_for_api.lumi}`}
          width={params_for_api.width}
          height={params_for_api.height}
        /> */}
      </StyledPlotRow>
    </StyledCol>
  );
};
